.input {
    -moz-appearance: none;
    -webkit-appearance: none;
    box-shadow: inset 0 0.0625em 0.125em rgba(10, 10, 10, 0.05);
    max-width: 100%;
    width: 90%;
    background-color: #141414;
    border-radius: 4px;
    color: #999fa1;
    border: 1px solid #999fa1;
    font-size: 1rem;
    line-height: 1.5;
    padding: calc(0.5em - 1px) calc(0.75em - 1px);
}
